import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi, App_detailsApi, Contractor_profileApi, EmailApi} from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';


declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'add-contractor.component.html'
})
export class AddContractorComponent {
	//@ViewChild('name') name: NgModel;
	//@ViewChild('priority') priority: NgModel;
	@ViewChild('contractorNameId') contractorNameId :NgModel;
	@ViewChild('contractorEmail') contractorEmail : NgModel;
	@ViewChild('idcontractorApp') idcontractorApp : NgModel;
	
	
	display: boolean = false;
	result: any;
	submitted: boolean = false;
	bookName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	//description: any = "";
	image: any;
	imgErrDisplay: boolean = false;
	loading: boolean = false;
	imgErr: any = '';
	data: any;
	allImageDataArr: any = [];
	imgName:any;
	emailValidate : boolean = true;

	emailRegex : RegExp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
	appDataArr :any[]=[];
	contractorName:any='';
	contractorEmailId:any='';
	profileImage:any;
	contractorProfileImage:any;
	contractorAppId:any[]=[];
	appGroup:any;
	contractor_status=false;

	//bookPriority:Number=0;
	constructor(private loopAuth: LoopBackAuth, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService,private appDetailsApi:App_detailsApi,private contractorApi:Contractor_profileApi,public emailApi:EmailApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;


	}

	showDialog() {
		this.display = true;
	}

	capital_letter(str) 
	{
		if(str != undefined && str != null && str != ''){

		
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}

			return str.join(" ");
		}
	}

	emailChange(){
		this.emailValidate = true;
	}
	
	async submit(contractorForm) {
		this.submitted = true;
		let validFlag = true;
		let uploadFlag1: any = true;
		
		
		


			if(contractorForm.valid){	

				let contractorName = this.capital_letter(contractorForm.value.contractorName);
				let emailId = (contractorForm.value.contractorEmailId != null && contractorForm.value.contractorEmailId != undefined && contractorForm.value.contractorEmailId != '')?contractorForm.value.contractorEmailId.toLowerCase():'';
				
				this.loading = true;
				
				this.emailApi.validateEmail({"email_id":emailId}).subscribe((result)=>{
				this.emailValidate = result.result.isValid;
					
				if(this.emailValidate){

				

						let contractorObj = {
							name_of_contractor : contractorName,//this.capital_letter(contractorForm.value.contractorName),
							contractor_email_id : emailId,//contractorForm.value.contractorEmailId.toLowerCase(),
							contractor_photo : '',
							app_id : (this.contractorAppId.length > 0)?this.contractorAppId:[],
							status: (this.contractor_status)?'Active':'Inactive'
							
						}
				

						//console.log("form submited",contractorForm);
						//console.log("form submited obj",contractorObj);
						this.ifNameExist(contractorObj).then(async (total)=>{
							if(total > 0){
								this.loading = false;
								this.result = "Contractor email id already exist";
								this.display = true;
							}else{
								await this.contractorApi.add_contractor(contractorObj).subscribe(()=>{
									//console.log("Contractor added successfully");
									this.contractorName = '';
											this.contractorEmailId = '';
											this.profileImage = '';
											this.contractorAppId = [];
									this.loading = false;
									this.result = 'success';
									this.showDialog();
								},error=>{
									if(error.message == 'user result already exist'){
										this.result = 'Contractor already exist';
										this.loading = false;
										this.showDialog();
									}else{
										//console.log("Add contractor error occured",error);
										this.result = 'Something went wrong';
										this.loading = false;
										this.showDialog();
									}
								});
								
							}
						});
					}else{
						this.loading = false;
					}
				},(err)=>{
					//console.log("Validate email error :",err);
					this.result = 'Something went wrong';
									this.loading = false;
									this.showDialog();
				});

			}else{
				this.loading = false;
			}


		

		
		
	}

	
	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{

				this.contractorApi.getContractorEmailCount({ "contractor_email_id": dataObj.contractor_email_id}).subscribe((data)=>{
					resolve(data.result[0].total);
				},(err)=>{
					reject(err);
				});

			}catch(err){
				reject(err);
			}
		});
		
		
	}

	ngOnInit(): void {
		// var dataArrObj = {
		// 	"limit":1
		// }
		// this.busy = this.books.getBooks(dataArrObj, null).subscribe(data=>{
		// 	if(data){
		// 		if(data.result.length == 1){
		// 			let priorityVal = data.result[0].priority;
		// 			this.bookPriority = priorityVal+1;
		// 		}
		// 	}
		// });
		this.loading = true;
		this.appDetailsApi.find({"where":{"status":1}}).subscribe(appResultdata=>{
			this.loading = false;
			if(appResultdata.length){
				for (const key in appResultdata) {
					let appObj = {
						appId : appResultdata[key]['app_id'],
						appName : appResultdata[key]['app_name'],
					}
					
					this.appDataArr.push(appObj);
				}
			}
			//console.log("app Array",this.appDataArr);
			//console.log("App Result data",appResultdata);
		},err =>{
			this.loading = false;
		});

	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["contractor"]);
	}

	onFileChange(file) {
		//console.log("file", file);
		this.imgErrDisplay = false;
		this.data = new FormData();
		var imageData = new FileReader();
		if (file.target.files[0]) {
			var fileSize = Math.round((file.target.files[0].size) / 1024);
			var imgExt = file.target.files[0].name.split(".");
			//console.log("file.target.files[0].type",file.target.files[0].type);
			if (imgExt.length > 2) {
				this.imgErr = "Invalid Image Format / Image name";
				this.imgErrDisplay = true;
			} else if (((file.target.files[0].type) != "image/jpg") && ((file.target.files[0].type) != "image/png") && ((file.target.files[0].type) != "image/jpeg")) {
				this.imgErr = "Invalid Image Format";
				this.imgErrDisplay = true;
			} else if (fileSize > 7000) {//chk img size
				this.imgErr = "Invalid Image Size";
				this.imgErrDisplay = true;
			} else {
				imageData.onload = (event: any) => {
					var img = new Image();
					img.src = event.target.result;
					if (img.width > 500 || img.height > 500) {
						this.imgErr = "Invalid Image Dimension. It needs to be of max 500*500";
						this.imgErrDisplay = true;
					} else {
						let targetFile = file.target.files[0];
						let fileExtension = '.' + targetFile['name'].split('.').pop();
						let filename = Math.random().toString(36).substring(7) + new Date().getTime() + fileExtension;
						this.data.append("file", file.target.files[0], filename);
						this.allImageDataArr.push(this.data);
						//this.contractorProfileImage = this.data;
					}
				}
				imageData.readAsDataURL(file.target.files[0]);
			}
		}
	}

	uploadImage(fdata) {
		return new Promise((resolve, reject) => {
			//console.log("fdata", fdata);
			let fileUrl = hostname+"/api/containers/contractorImage";
			//let fileUrl = "http://phapi.experiencecommerce.com:4500/api/containers/logo_image";
			let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
			//this.loading = true;
			this.http.post(fileUrl + '/upload?access_token='+loginFlagToken, fdata).subscribe(res => {
				var dataimg = res['_body'];
				dataimg = JSON.parse(dataimg);
				if(dataimg){
					this.imgName = dataimg['result']['files']['file'][0]['name'];
					// this.imgName = fileUrl+"/download/"+imgName;
					//this.loading = false;
					resolve(true);
				}else{
					//this.loading = false;
					resolve(false);
				}
			},err => {
				this.imgErr = "Image upload failed";
				//this.imgshow = this.defaultImg;
				this.imgErrDisplay = true;
				//this.loading = false;
				resolve(false);
			},
			() => {

			});
		});
	}

	contractorStatusChanged(event){
		if(!this.contractor_status){
			this.contractorAppId = [];
		}
	}
}


